@keyframes portal-animation {
	from {
		transform: rotate(0) scale(1);
	}
	12% {
		transform: rotate(90deg) scale(1.4);
	}
	25% {
		transform: rotate(180deg) scale(1.1);
	}
	38% {
		transform: rotate(270deg) scale(1.4);
	}
	49% {
		transform: rotate(360deg) scale(1.2);
	}
	64% {
		transform: rotate(270deg) scale(1.5);
	}
	73% {
		transform: rotate(180deg) scale(1.1);
	}
	85% {
		transform: rotate(90deg) scale(1.3);
	}
	to {
		transform: rotate(360deg) scale(1);
	}
}

.wrapper {
	margin-right: auto; /* 1 */
	margin-left: auto; /* 1 */
	max-width: 1400px;
}

.nav {
	position: fixed;
	top: 0;
	background-color: #00693e;
	background: rgb(66, 135, 106);
	background: radial-gradient(circle, rgba(66, 135, 106, 1) 4%, rgba(63, 143, 110, 1) 18%, rgba(1, 140, 83, 1) 93%);
	width: 100%;
	overflow: hidden;
	height: 70px;
	z-index: 1000;
	button {
		background-color: transparent;
		border: none;
		cursor: pointer;
		svg {
			padding: 0.3em;
			font-size: 4rem;
		}
	}
	&__container {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__logo {
		display: flex;
		align-items: center;
		padding: 1.5em;
		margin-left: 20px;
		color: #d5e9e3;

		&-icon {
			font-size: 4rem;
			margin-right: 0.3em;
			animation: portal-animation 20s linear infinite;
		}
		&-text {
			font-size: 3rem;
			font-weight: bold;
		}
	}
	&__menu {
		display: flex;
		flex-direction: row-reverse;
		align-items: flex-start;
		justify-content: space-between;
		position: fixed;
		top: 0;
		right: 0%;
		transform: translateX(100%);
		background-color: black;
		height: 100vh;
		width: 100%;
		transition: transform 0.3s;
		&--active {
			transform: translateX(0%);
		}
		&-show-icon {
			color: #d5e9e3;
		}
		&-close-icon {
			color: whitesmoke;
		}
	}
}
@media (min-width: 1000px) {
	.nav {
		overflow: visible;
		&__menu {
			position: absolute;
			top: 0;
			right: 10px;
			min-width: 700px;
			max-width: 1000px;
			height: 70px;
			background-color: unset;
			flex-direction: row;
			transform: translateX(0);
			transition: transform 0s;

			&-show-icon {
				display: none;
			}
			&-close-icon {
				display: none;
			}
		}
	}
}
