.menu-list {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	list-style: none;
	height: 100%;
	.categories {
		color: white;
		&-text {
			font-size: 3rem;
			padding: 0;
		}
		&-list {
			background-color: black;
			&__category {
				width: 70%;
				border-bottom: 2px solid rgba(192, 192, 192, 0.2);
			}
		}
	}
	li {
		a {
			color: whitesmoke;
			text-decoration: none;
			font-size: 3rem;
		}
		svg {
			font-size: 3rem;
			margin: 0 0.5em;
		}
	}
}
@media (min-width: 1000px) {
	.menu-list {
		position: relative;
		flex-direction: row;
		align-items: center;
		height: 70px;
		background-color: transparent;
		margin-right: 20px;
		.categories {
			color: white;
			&-text {
				font-size: 1.8rem;
			}
			&-list {
				position: absolute;
				top: 70px;
				left: 0;
				width: 25%;
				height: 200px;
				background-color: rgb(66, 135, 106);
				&__category {
					width: 70%;
					border-bottom: 2px solid rgba(192, 192, 192, 0.2);
				}
			}
		}
		li {
			margin: 0 1em;
			a,
			svg {
				font-size: 1.8rem;
			}
		}
	}
}
